/*
  Used to manage styles related to reusable components / widgets.

  1. Pickers - used to pick complex data types
  2. Number Increment
  3. Tree - used for tree control
  4. Person List - used to show a list of people
  5. Rollover - Used to hide/show items in the UI has the mouse rolls-over a container.
  6. Pagelist - used to list child pages; currently used in admin screens
  7. Pill Help (used in security dialog)
  8. Tags - component that allows you to tag entities
  9. Photo Frame - wraps a nice border around photos
  10. Smart Search - search at th top of the page for searching people, groups, etc.
  11. Fieldset Actions - styling for the button actions on field sets
  12. Title - markup for styling header titles
  13. Code Editor - styling for code editor
  14. Labels - additional label defintions that extend Boostrap labels (eg label-campus)
  15. Date Picker - styles for the date picker
  16. Time Picker - styles for the time picker
  17. File Upload / Image Upload - styles for the file / image uploader
  18. Update Panel Actions - annimation for update panels
  19. Group List - Reusable styles for listing group members (family view/edit in CRM Person Profile)
  20. Well Messgae - A larger alert type function for delivering important messages.
  21. Security Controls - login status etc
  22. Rock List - styles for the Rock list component
  23. TypeAhead - styles for type ahead support
  24. Image Cropper - styles for image crop component
  25. Active Users - styles for the active users block
  26. Navigation for Paging - styles for creating a pager
  27. List Extensions - additional list options
  28. Key Value Pair Control - styling for key/value pair control
  29. Context Setters
  30. Paneled List Group - extends the bootstrap list group to behave in a panel
  31. Sliding Date Range
  32. Photo Round
  33. Wizard
  34. Checkbox
  35. Follow button
  36. Range Editors
  37. Custom Buttons
  38. Color Picker
  39. HTML Editor (summernote)
  40. Person Directory
*/

// 1. Pickers
// -------------------------

.picker {
  .clearfix();
  @media @sm {
    position: relative;
    width: 250px; // needed when floated right to keep the picker from shifting when the (x) appears to allow delete
  }

  .picker-mode-options {
    margin-bottom: 12px;
  }

  .picker-label {
    display: inline-block;
    float: left;
    width: 225px;
    padding: @padding-base-vertical @padding-base-horizontal;
    font-size: @font-size-base;
    color: @input-color;
    background-color: @input-group-addon-bg;
    border: 1px solid @input-group-addon-border-color;
    border-radius: @border-radius-base;

    i {
      float: left;
      margin-top: 2px;
      margin-right: 6px;
    }

    span {
      float: left;
      width: 140px;
      margin-right: 6px;
      overflow: hidden;
      line-height: 18px;
      text-overflow: ellipsis;
      white-space: nowrap;
    }

    .fa.fa-caret-down {
      margin-top: 3px;
    }
  }

  .picker-select-none {
    display: block;
    float: left;
    margin: 6px 0 0 6px;
    cursor: pointer;
  }

  .scroll-container-picker {
    width: 355px;

    .viewport {
      width: 340px;
      height: 380px;
    }

    .scrollbar {
      margin-top: 10px;
    }
  }

  h4 {
    font-size: 14px;
    font-weight: @font-weight-semibold;
  }

  .picker-menu {
    width: 400px;
    padding: 10px;

    h4:first-child {
      margin-top: 0;
    }
      @media (max-width: @screen-xs-max) {
        position: static;
        max-width: 100%;
      }
  }

  &.picker-menu-right {
    .picker-menu {
      right: 0;
      left: auto;
    }
  }

  .picker-actions {
    padding: 10px;
    margin: 10px -10px -10px -10px;
    background-color: @picker-action-bg;
    border-top: 1px solid darken(@picker-action-bg, 10%);
    border-radius: 0 0 @border-radius-base @border-radius-base;
  }
  // details used for pickers like person picker to show the details of the selected item
  .picker-select-item-details {
    margin-left: 20px;

    .person-image {
      display: inline-block;
      float: left;
      float: left;
      width: 70px;
      height: 70px;
      margin-right: 7.5px;
      border: 1px solid @photo-border;
    }

    .contents {
      float: left;
      margin-left: 10px;

      h5 {
        margin-bottom: 2px;
      }
    }
  }

  &.picker-geography {
    .picker-menu {
      width: 520px;
    }
  }

  &.picker-person {
    .picker-select {
      padding: 0;
      list-style: none;
    }

    .picker-select-item-details img {
      width: 65px;
    }
  }

  &.picker-novalue {
    // used for pickers that do not retain values to remove the extra spacing needed for the 'x'
    .picker-label {
      width: 100%;
    }
  }

  &.picker-mergefield {
    width: 370px;
  }
}

#dialog .scroll-container-picker {
  width: 360px;

  .viewport {
    width: 340px;
    height: 190px;
  }
}

// date time picker
.date-time-picker {
  margin-bottom: 11px;

  .input-group {
    margin-bottom: 4px;
  }
}

// campuses picker

.campuses-picker {
  .clearfix();

  .checkbox {
    float: left;
    margin-top: 0;
    margin-right: 24px;
  }
}


.campuses-picker .campuses-picker-vertical .checkbox {
  float: none;
  margin-top: inherit;
  margin-right: 0;
}


// 2. Number Increment
// -------------------------
.numberincrement {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
       -o-user-select: none;
          user-select: none;

  .numberincrement-up,
  .numberincrement-down {
    padding: 6px 12px;
    color: @input-color;
    cursor: pointer;
    background-color: @input-group-addon-bg;
    border: 1px solid @input-group-addon-border-color;

    &.disabled {
      color: lighten(@input-color, 20%);
      cursor: default;
    }
  }

  .numberincrement-down {
    border-radius: @border-radius-base 0 0 @border-radius-base;
  }

  .numberincrement-up {
    border-radius: 0 @border-radius-base @border-radius-base 0;
  }

  .numberincrement-value {
    padding: 6px 18px;
    border-color: @input-group-addon-border-color;
    border-style: solid;
    border-width: 1px 0;
  }
  // adjustments for different sizes
  &.input-sm {
    .numberincrement-up,
    .numberincrement-down {
      padding: 5px 8px;
    }

    .numberincrement-value {
      padding: 5px 12px;
    }
  }

  &.input-lg {
    .numberincrement-up,
    .numberincrement-down {
      padding: 12px 20px;
    }

    .numberincrement-value {
      padding: 12px 28px;
    }
  }
}



// 3. Tree
// -------------------------
ul.rocktree,
ul.rocktree ul {
  padding: 0;
  margin-bottom: @spacing-lg-px;
  list-style-type: none;
}

ul.rocktree ul {
  margin: 0 0 0 @spacing-md-px;
}

.rocktree {
  .rocktree-item {
    white-space: nowrap;
    &.is-inactive {
      color: lighten(@text-color, 20%);
      opacity: 1;
    }

    &:not(.is-inactive) {
      color: @text-color;
    }

    & > i {
      padding: 4px;
      cursor: pointer;
    }
  }

  li {
    margin: 0;
  }

  span,
  a {
    display: inline-block;
    margin: 1px;
  }

  .rocktree-name:hover,
  span:hover,
  span:hover a {
    color: @tree-rollover-color;
    background: @tree-rollover-bg;
  }

  .selected,
  .selected a {
    color: @tree-selected-color;
    background: @tree-selected-bg;
  }

  .rocktree-name {
    padding: 2px 6px;
    cursor: pointer;
    border-radius: 3px;
  }

  .rocktree-loading {
    margin-left: 10px;
    font-size: 12px;
    color: @text-color !important;

    i {
      margin-right: 5px;
    }
  }

  .rocktree-leaf {
    padding-left: 18px;
  }
}

// treeview wrapper
.treeview {
  margin-bottom: @spacing-md-px;
  .treeview-frame {
    padding-left: 0;
  }

  .treeview-actions {
    margin-bottom: @spacing-sm-px;

    .btn {
      margin-bottom: 4px;
    }
  }
}



// 4. Person List
// -------------------------

.personlist {
  padding-left: 0;
  list-style: none;

  li {
    padding-left: @spacing-md-px;

    i.fa-circle {
      margin-right: @spacing-sm-px;
      margin-left: -@spacing-md-px;
      font-size: 9px;
      color: #d0cfcf;
    }

    .actions {
      opacity: 0;
      -webkit-transition: opacity .5s ease-out;
         -moz-transition: opacity .5s ease-out;
           -o-transition: opacity .5s ease-out;
              transition: opacity .5s ease-out;
    }

    &:hover {
      .actions {
        opacity: 1;
      }
    }
  }

  .popover {
    width: 320px;
  }
}

// 5. Rollover
// -------------------------

.rollover-container {
  position: relative;

  & > .rollover-item {
    opacity: 0;
    -webkit-transition: opacity .5s ease-out;
       -moz-transition: opacity .5s ease-out;
         -o-transition: opacity .5s ease-out;
            transition: opacity .5s ease-out;
  }

  &:hover {
    & > .rollover-item {
      opacity: 1;
    }
  }
}

// always show item if on touch device
.touch .rollover-item {
  opacity: 1;
}


// 6. Pagelist
// -------------------------

.block-title {
  margin: 0;
}

.list-as-blocks {
  h3 {
    margin: 6px 0;
    margin-top: 0;
    font-size: 14px;
  }

  ul {
    padding: 0;
    margin-top: 16px;
    list-style: none;

    li {
      position: relative;
      float: left;
      width: 47.5%;
      margin: 0 1.25% @spacing-md-px;
      overflow: hidden;
      overflow: visible;
      text-align: center;
      border: 1px solid @listasblocks-border-color;
      border-radius: @border-radius-base;

      a {
        display: block;
        height: 90px;
        padding: @spacing-md-px;
        color: @listasblocks-color;
        background-color: @listasblocks-bg;
        border-radius: @border-radius-base;
        -webkit-transition: background-color 200ms linear;
           -moz-transition: background-color 200ms linear;
            -ms-transition: background-color 200ms linear;
             -o-transition: background-color 200ms linear;
                transition: background-color 200ms linear;

        i {
          margin-top: 6px;
          font-size: 26px;
          color: lighten(@listasblocks-color, 10%);
        }

        &:hover {
          color: @listasblocks-rollover-text-color;
          background-color: @listasblocks-rollover-bg;
          border-color: @listasblocks-rollover-border-color;

          i {
            color: lighten(@listasblocks-rollover-text-color, 10%);
          }
        }

        div.notification {
          position: absolute;
          top: -10px;
          right: -6px;

          .label {
            padding: 2px 6px;
            border-radius: 36px;
          }
        }
      }

      &.active {
        a {
          color: @listasblocks-active-color;
          background-color: @listasblocks-active-bg;
          border-color: @listasblocks-active-border-color;

          i {
            color: lighten(@listasblocks-active-color, 10%);
          }
        }
      }
    }
  }

  .block-status a {
    height: 105px;
    padding: 8px 15px;

    .status-list {
      position: absolute;
      right: 0;
      bottom: 5px;
      left: 0;
    }
  }
}

@media @sm-only {
  // tablet portrait and larger
  .list-as-blocks {
    ul {
      li {
        width: 32%;
        margin: 0 1% @spacing-md-px;

        &:nth-child(3n) {
          margin-right: 0;
        }

        &:nth-child(3n+1) {
          margin-left: 0;
        }
      }
    }
  }
}

@media @md {
  // tablet portrait and larger
  .list-as-blocks {
    ul {
      li {
        width: 18.4%;
        margin: 0 1% @spacing-md-px;

        &:nth-child(5n) {
          margin-right: 0;
        }

        &:nth-child(5n+1) {
          margin-left: 0;
        }
      }
    }
  }
}

// 7. Pill Help (used in security dialog)
// -------------------------

.nav > li.pill-help {
  a {
    color: @nav-pills-link-color;

    &:hover,
    &:focus {
      color: @nav-pills-link-color;
      background-color: transparent;
    }
  }
}

.well-pillwrap {
  background-color: @nav-pills-container-bg;
  border-color: @nav-pills-container-border;
}

// 8. Tags
// ------------------------

div.tagsinput {
  float: left;
  width: 100%;
  overflow-y: auto;
}

.taglist {
  text-align: center;

  .tag {
    position: relative;
    display: inline-block;
    height: 22px;
    padding: 1px 10px 0 12px;
    margin-bottom: @spacing-sm-px;
    margin-left: 20px;
    font-size: 13px;
    line-height: 18px;
    color: @tag-text-color;
    text-decoration: none;
    background: @tag-bg;
    border-top-right-radius: @tag-border-radius;
    border-bottom-right-radius: @tag-border-radius;

    &:first-child {
      margin-left: 10px;
    }

    span {
      display: block;
      float: left;
      max-width: 120px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }

    a {
      display: block;
      float: left;
    }

    small {
      font-size: 11px;
    }

    &:before {
      position: absolute;
      top: 0;
      left: -10px; // chrome looks better with -11px but IE has line if so
      float: left;
      width: 0;
      height: 0;
      /* tag point */
      content: "";
      border-color: transparent @tag-bg transparent transparent;
      border-style: solid;
      border-width: 11px 11px 11px 0;
    }

    &:after {
      position: absolute;
      top: 8px;
      left: 0;
      float: left;
      width: 5px;
      height: 5px;
      /* tag dot */
      content: "";
      background: @tag-dot-color;
      border-radius: 2px;
      box-shadow: -1px -1px 2px @tag-bg;
    }
  }
}

div.tagsinput {
  div[id$=addTag] {
    @media @sm {
      float: left;
      margin-left: 12px;
    }
  }

  span.tag.personal {
    background: @tag-bg-alt;
    &:before {
      border-color: transparent @tag-bg-alt transparent transparent;
    }
  }

  span.tag a {
    font-size: 11px;
    font-weight: bold;
    color: @tag-text-color;
    text-decoration: none;
    opacity: 0;
    -webkit-transition: opacity .3s ease 0s, color .3s ease 0s, background .3s ease 0s;
       -moz-transition: opacity .3s ease 0s, color .3s ease 0s, background .3s ease 0s;
  }

  span.tag:hover a {
    opacity: 1;
  }

  input {
    display: block;
    width: 80px;
    padding: 0;
    padding: 2px;
    margin: 0;
    margin: -2px 0 0 10px;
    margin: 0 auto;
    clear: both;
    font-family: helvetica;
    font-size: 13px;
    color: #8f8f8f !important; /* needed as script adds element value */
    background: transparent;
    border: 1px solid transparent;
    border-radius: @border-radius-base;
    outline: 0;
    box-shadow: none;

    &:hover,
    &:focus {
      background-color: rgba(0,0,0,.05);
    }

    div {
      display: block;
      float: left;
    }
  }

  .tags_clear {
    width: 100%;
    height: 0;
    clear: both;
  }
}

.not_valid {
  color: #90111a !important;
  background: #fbd8db !important;
}

.tag-wrap {
  .ui-autocomplete {
    position: absolute;
    top: 4px;
    left: 0;
    z-index: 90;
    z-index: 9999 !important;
    min-width: 200px;
    padding: 12px;
    list-style: none;
    background-color: @autocomplete-bg;
    border: 1px solid @autocomplete-border;
    border-radius: 0 0 @border-radius-base @border-radius-base;
    .box-shadow(0 5px 10px rgba(0,0,0,.2));

    li {
      margin-bottom: 4px;

      &:last-child {
        margin-bottom: 0;
      }

      a {
        display: block;
        padding: 4px;
        color: @autocomplete-text;
        text-decoration: none;
        cursor: pointer;

        &:hover {
          background: @autocomplete-rollover-bg;
          border-radius: 4px;
        }
      }
    }
  }
}

.tag-letterlist {
  color: lighten(@text-color, 20%);

  li {
    margin-right: 12px;
  }

  a {
    font-weight: @font-weight-semibold;
  }
}

/* Medium devices (desktops, 992px and up) */
@media (min-width: @screen-sm-min) {
  .taglist {
    text-align: left;

    .tag {
      float: left;
    }
  }

  div.tagsinput input {
    margin: 0;
  }
}

// 9.
// ------------------------
.photoframe {
  display: inline-block;
  border: 4px solid @photo-border;

  &.photoframe-sm {
    border-width: 2px;
  }
}


// 10. Smart Search
// ------------------------

.smartsearch {
  position: relative;
  width: 290px;
  padding: 2px 6px;
  margin-top: 6px;
  font-size: 12px;
  color: @smartsearch-text-color;
  background-color: @smartsearch-bg;
  border: 1px solid @smartsearch-border-color;
  border-radius: @border-radius-base;

  input::-moz-placeholder,
  input::-webkit-input-placeholder {
    color: darken(@smartsearch-input-text-color, 25%);
  }

    .nav > li > a {
      padding: 4px 15px;
    }

      input::selection,
      input::-moz-selection {
        color: @smartsearch-input-selection-text-color;
        background: @smartsearch-input-selection-background;
      }

  &:focus {
    border-color: #595959;
  }

  .twitter-typeahead {
    display: block !important;
    margin-right: 120px;
  }

  .smartsearch-type {
    width: 120px;
  }

  i {
    position: absolute;
    float: left;
    margin-top: 6px;
    font-size: 16px;
    opacity: .6;
  }

  input {
    width: 150px;
    margin: 0;
    font-size: 14px;
    color: @smartsearch-input-text-color;
    background: transparent;
    border: 0;
    border-radius: 0;

    &:focus {
      outline: 0; // get rid of orange box in chrome
      box-shadow: 0 0 0 0;
    }
  }

  .tt-hint {
    padding: 4px 4px 4px 25px;
  }

  .tt-dropdown-menu {
    min-width: 290px;
    margin-top: 2px;
    margin-left: -6px;
    border: 0;
  }

  input.searchinput {
    width: 100%;
    padding: 4px 4px 4px 25px;
    color: @smartsearch-input-text-color;
    border: 0 !important;
    box-shadow: none !important;
  }

  .dropdown {
    .navbar-link {
      color: @smartsearch-text-color;
      text-align: right;

      span {
        padding-right: 4px;
      }

      &:hover,
      &:focus {
        background-color: transparent;
      }
    }

    &.open {
      .navbar-link {
        background-color: transparent;
      }
    }

    .dropdown-menu {
      right: 0;
      left: auto;
      // smart search type menu
      font-size: 12px;
    }
  }
}

