// 15. Datepicker
// -------------------------

.datepicker {
  padding: 10px;

  &.datepicker-dropdown {
    &:before {
      position: absolute;
      display: inline-block;
      content: '';
      border-top: 0;
      border-right: 7px solid transparent;
      border-bottom: 7px solid #ccc;
      border-bottom-color: rgba(0,0,0,.2);
      border-left: 7px solid transparent;
    }

    &:after {
      position: absolute;
      display: inline-block;
      content: '';
      border-top: 0;
      border-right: 6px solid transparent;
      border-bottom: 6px solid #fff;
      border-left: 6px solid transparent;
    }

    &.datepicker-orient-left {
      &:before {
        left: 6px;
      }
      &:after {
        left: 7px;
      }
    }

    &.datepicker-orient-right {
      &:before {
        right: 6px;
      }

      &:after {
        right: 7px;
      }
    }

    &.datepicker-orient-top {
      &:before {
        top: -7px;
      }

      &:after {
        top: -6px;
      }
    }

    &.datepicker-orient-bottom {
      &:before {
        bottom: -7px;
        border-top: 7px solid #999;
        border-bottom: 0;
      }
      &:after {
        bottom: -6px;
        border-top: 6px solid #fff;
        border-bottom: 0;
      }
    }
  }

  table {
    background-color: transparent;

    thead {
      th.datepicker-switch {
        text-align: center;
      }

      th.next {
        text-align: right;
      }
    }

    td.active,
    td.today {
      color: @datepicker-highlight-color;
      background-color: @datepicker-highlight-bg;
    }

    td.day {
      text-align: center;
      cursor: pointer;
    }

    td.old, td.new {
      color: lighten(@text-color, 40%);
    }

    tfoot {
      display: none;
    }

    td span.month {
      padding: 2px;
      margin-left: 2px;
      cursor: pointer;

      &.active {
        font-weight: @font-weight-bold;
      }

      &:hover {
        color: @datepicker-highlight-color;
        background-color: @datepicker-highlight-bg;
      }

      &:first-child {
        margin-left: 0;
      }
    }

    td span.year {
      padding: 2px;
      margin-left: 4px;
      cursor: pointer;

      &.active {
        font-weight: @font-weight-bold;
      }

      &:hover {
        color: @datepicker-highlight-color;
        background-color: @datepicker-highlight-bg;
        border-radius: @border-radius-small;
      }

      &:first-child {
        margin-left: 0;
      }
    }
  }
}

// 16. Time Picker
// -------------------------

.timepicker-input {
  .clearfix();

  .input-group {
    float: left;
  }

  & i.fa-times {
    float: left;
    padding: 6px;
    margin-top: 4px;
    cursor: pointer;
    opacity: 0;
    -webkit-transition: opacity .2s ease-out;
       -moz-transition: opacity .2s ease-out;
         -o-transition: opacity .2s ease-out;
            transition: opacity .2s ease-out;
  }

  &:hover i.fa-times {
    opacity: 1;
  }
}

.touch .timepicker-input i.fa-times {
  opacity: 1;
}

.bootstrap-timepicker {
  position: relative;

  &.pull-right {
    .bootstrap-timepicker-widget {
      &.dropdown-menu {
        right: 0;
        left: auto;

        &:before {
          right: 12px;
          left: auto;
        }

        &:after {
          right: 13px;
          left: auto;
        }
      }
    }
  }

  .add-on {
    cursor: pointer;

    i {
      display: inline-block;
      width: 16px;
      height: 16px;
    }
  }
}

.bootstrap-timepicker-widget {
  &.dropdown-menu {
    padding: 2px 3px 2px 2px;

    &.open {
      display: inline-block;
    }

    &:before {
      position: absolute;
      top: -7px;
      left: 9px;
      display: inline-block;
      content: "";
      border: 7px solid transparent;
      border-bottom-color: rgba(0, 0, 0, .2);
    }

    &:after {
      position: absolute;
      top: -6px;
      left: 10px;
      display: inline-block;
      content: "";
      border: 6px solid transparent;
      border-bottom-color: #fff;
    }
  }

  a.btn, input {
    border-radius: 4px;
  }

  table {
    width: 100%;
    margin: 0;
    background-color: transparent;

    td {
      height: 30px;
      padding: 2px;
      margin: 0;
      text-align: center;

      &:not(.separator) {
        min-width: 30px;
      }

      span {
        width: 100%;
      }

      a {
        display: inline-block;
        width: 100%;
        padding: 8px 0;
        margin: 0;
        color: #333;
        border: 1px transparent solid;
        outline: 0;

        &:hover {
          text-decoration: none;
          background-color: #eee;
          border-color: #ddd;
                  border-radius: 4px;

          -webkit-border-radius: 4px;
             -moz-border-radius: 4px;
        }

        i {
          margin-top: 2px;
        }
      }

      input {
        width: 45px;
        padding: @padding-base-vertical @padding-base-horizontal;
        margin: 0;
        font-size: 12px;
        text-align: center;
        border: 1px solid @input-border;
        border-radius: @border-radius-base;

        .box-shadow(inset 0 1px 1px rgba(0,0,0,.075));
        .transition(~"border-color ease-in-out .15s, box-shadow ease-in-out .15s");
      }
    }
  }
}

.bootstrap-timepicker-widget .modal-content {
  padding: 4px;
}

@media (min-width: 767px) {
  .bootstrap-timepicker-widget.modal {
    width: 200px;
    margin-left: -100px;
  }
}

@media (max-width: 767px) {
  .bootstrap-timepicker {
    width: 100%;

    .dropdown-menu {
      width: 100%;
    }
  }
}

// 17. Upload Controls
// -------------------------

.imageupload-group {
  position: relative;
  display: table-cell;
  padding: 2px;
  margin-bottom: @spacing-md-px;
  cursor: pointer;
  border: 1px solid @photo-border;
  border-radius: @border-radius-base;

  .imageupload-thumbnail {
    position: relative;

    .imageupload-thumbnail-image {
      width: 100%;
      height: 100%;
      background-position: 50%;
      background-size: cover;
    }

    .imageupload-remove {
      position: absolute;
      right: 0;
      bottom: 0;

      a {
        display: block;
        padding: 2px 6px;
        color: @btn-danger-color;
        cursor: pointer;
        background-color: @btn-danger-bg;
        border-top-left-radius: @border-radius-base;
      }
    }
  }

  .imageupload-dropzone {
    position: relative;
    width: 100%;
    height: 30px;
    padding-top: 8px;
    margin-top: 2px;
    line-height: 12px;
    text-align: center;
    cursor: pointer;
    background-color: @input-group-addon-bg;
    border: 1px solid @input-border;
    border-radius: 0 0 @border-radius-base @border-radius-base;

    ::-webkit-file-upload-button {
      cursor: pointer;
    }

    input {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      cursor: pointer;
      opacity: 0;
    }

    span {
      width: 100%;
      font-size: 13px;
      color: @input-color;

      &:before {
        padding-right: .25em;
        content: @fa-var-upload;

        .fa-icon();
      }
    }
  }

  .upload-progress {
    position: absolute;
    top: 30px;
    left: 35px;
    z-index: 2;
    opacity: .3;
  }
}

.fileupload-group {
  position: relative;
  display: table-cell;
  padding: 2px;
  margin-bottom: @spacing-md-px;
  cursor: pointer;
  border: 1px solid @input-border;
  border-radius: @border-radius-base;

  .fileupload-thumbnail {
    position: relative;

    .file-link {
      display: block;
      width: 100px;
      height: 50px;
      padding-top: 2px;
      overflow: hidden;
      font-size: 10px;
      color: @input-color;
      text-align: center;
      text-overflow: ellipsis;
      white-space: nowrap;
      background-color: @input-group-addon-bg;
      border-radius: (@border-radius-base - 1);

      &:after {
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -20px;
        margin-left: 42px;
        margin-left: -12px;
        font-family: FontAwesome;
        font-size: 28px;
        color: darken( @input-group-addon-bg, 15%);
        content: '\f016';

        -webkit-font-smoothing: antialiased;
      }

      &.file-exists:after {
        margin-top: -10px;
        color: darken( @input-group-addon-bg, 15%);
        content: '\f15c';
      }
    }
  }

  .fileupload-remove {
    position: absolute;
    right: 0;
    bottom: 0;

    a {
      display: block;
      padding: 2px 6px;
      color: @btn-danger-color;
      cursor: pointer;
      background-color: @btn-danger-bg;
      border-top-left-radius: @border-radius-base;
    }
  }

  .fileupload-dropzone,
  .fileupload-button {
    position: relative;
    height: 30px;
    padding-top: 8px;
    line-height: 12px;
    text-align: center;
    background-color: @input-group-addon-bg;

    ::-webkit-file-upload-button {
      cursor: pointer;
    }

    input {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      cursor: pointer;
      opacity: 0;
    }

    span {
      width: 100%;
      padding: 0 .5em;
      font-size: 13px;
      color: @input-color;

      &:before {
        padding-right: .25em;
        content: @fa-var-upload;

        .fa-icon();
      }
    }
  }

  .fileupload-dropzone {
    margin-top: 2px;
    border-radius: 0 0 @border-radius-base @border-radius-base;
  }

  .fileupload-button {
    padding: 6px 12px;
    background-color: @brand-primary;
    border: 1px solid @btn-primary-border;
    border-radius: @border-radius-base;

    span {
      color: #fff;
    }
  }

  .upload-progress {
    position: absolute;
    top: 8px;
    left: 35px;
    z-index: 2;
    opacity: .3;
  }
}



// 18. Update Panel Status
// -------------------------

.updateprogress-status {
  position: fixed;
  top: 40%;
  left: 50%;
  z-index: (@zindex-modal-background + 100);
  padding: 15px 10px;
  margin-left: -30px;
  background-color: @spinner-bg;
  border-radius: 120px;
  box-shadow: 5px 5px 12px 3px rgba(0,0,0,.1);
  opacity: 1;

  .spinner {
    width: 40px;
    height: 30px;
    font-size: 10px;
    text-align: center;
  }

  .spinner > div {
    display: inline-block;
    width: 5px;
    height: 100%;
    background-color: @spinner-color;
    -webkit-animation: stretchdelay 1.2s infinite ease-in-out;
            animation: stretchdelay 1.2s infinite ease-in-out;
  }

  .spinner .rect2 {
    -webkit-animation-delay: -1.1s;
            animation-delay: -1.1s;
  }

  .spinner .rect3 {
    -webkit-animation-delay: -1.0s;
            animation-delay: -1.0s;
  }

  .spinner .rect4 {
    -webkit-animation-delay: -.9s;
            animation-delay: -.9s;
  }

  .spinner .rect5 {
    -webkit-animation-delay: -.8s;
            animation-delay: -.8s;
  }

  @-webkit-keyframes stretchdelay {
    0%, 40%, 100% {
      -webkit-transform: scaleY(.4);
    }

    20% {
      -webkit-transform: scaleY(1.0);
    }
  }

  @keyframes stretchdelay {
    0%, 40%, 100% {
      -webkit-transform: scaleY(.4);
              transform: scaleY(.4);
    }

    20% {
      -webkit-transform: scaleY(1.0);
              transform: scaleY(1.0);
    }
  }
}

@media @md {
  .updateprogress-status {
    padding: 30px 20px;
    margin-left: -60px;

    .spinner {
      width: 80px;
      height: 60px;

      > div {
        width: 10px;
      }
    }
  }
}

